@import url(https://fonts.googleapis.com/css?family=Libre+Franklin&display=swap);
html body {
  margin: 0;
  padding: 0;
  font-family: 'Libre Franklin', sans-serif;
  letter-spacing: .06em;
  color: grey; }

ul {
  list-style-type: none; }

.container {
  width: 100%; }

.row {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0 20px 0 20px;
  margin: auto;
  max-width: 900px; }

nav {
  display: flex;
  position: fixed;
  width: 100%;
  height: 55px;
  padding-top: 5px;
  justify-content: center;
  align-items: center; }
  nav .navlink {
    padding-left: 10px;
    text-transform: uppercase;
    text-decoration: none; }

.signature {
  position: absolute;
  left: 0;
  top: 0;
  height: 75px;
  margin: 10px; }

a {
  text-transform: uppercase;
  text-decoration: none;
  color: grey; }

.projects {
  padding-top: 60px; }

.column {
  display: flex;
  text-decoration: none;
  margin: 10px 5px 30px 5px;
  width: 45%;
  max-width: 460px;
  max-height: 345px;
  border-radius: 10px; }

.thumbnail {
  width: 100%;
  height: 100%; }

.title {
  font-size: 1em;
  font-weight: 700;
  text-transform: uppercase;
  text-align: left;
  z-index: 999;
  margin: 5px 0 0 10px;
  text-transform: uppercase;
  text-decoration: none;
  color: grey; }

#mariaopoly {
  order: -5;
  justify-content: center;
  width: 90%;
  max-width: none;
  max-height: 500px;
  height: auto; }

#bookTracker,
#restaurantsReview {
  order: -6; }

#kitestring,
#demo {
  width: 45%;
  max-height: 250px; }

#dragoneaters {
  order: -2;
  width: 50%;
  max-width: 600px;
  align-self: center; }

#jak {
  order: -1;
  width: 35%;
  max-height: 500px; }

#gen {
  width: 30%;
  height: 250px;
  align-self: center; }

#mountain {
  order: 100;
  width: 60%;
  max-width: 700px;
  max-height: auto; }

.about {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  padding-top: 60px; }

.cover {
  width: 60%;
  height: 400px;
  align-self: center; }
  .cover img {
    width: 100%;
    height: 100%;
    border-radius: 10px; }

.info {
  width: 60%;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
  align-self: center;
  color: grey; }

.abouttitle {
  font-weight: 700;
  padding-top: 10px;
  font-size: 1.1em; }

.bio,
.skills {
  align-self: flex-start;
  width: 100%;
  padding: 10px; }

.skills {
  order: -1;
  text-align: right; }

.feats {
  font-size: 1em;
  text-transform: none; }

.social {
  margin: 5px;
  width: 30px;
  height: 30px; }

